<script>
import { onMount } from 'svelte';
import Tooltip from './Tooltip.svelte'
import {tooltip} from './tooltip.js'
import Icon from 'fa-svelte'
import { faWindowClose } from '@fortawesome/free-solid-svg-icons/faWindowClose'
import { faPlus } from '@fortawesome/free-solid-svg-icons/faPlus'
//import { faPlusSquareO } from '@fortawesome/free-solid-svg-icons/faPlusSquareO'
import { faDoorOpen } from '@fortawesome/free-solid-svg-icons/faDoorOpen'
import { faDoorClosed } from '@fortawesome/free-solid-svg-icons/faDoorClosed'
import { faWifi } from '@fortawesome/free-solid-svg-icons/faWifi'
import { faCheck } from '@fortawesome/free-solid-svg-icons/faCheck'
let iconFaWindowClose = faWindowClose;
let iconFaPlus = faPlus;
//let iconFaPlusSquareO = faPlusSquareO;
let iconFaDoorOpen = faDoorOpen;
let iconFaDoorClosed = faDoorClosed;
let iconFaWifi = faWifi;
let iconFaCheck = faCheck;
import {getNmcliConnectionShow, getNmcliDeviceHotspot
  , getNmcliDeviceWifiList
  , getNmcliDeviceWifiListCache
  , postNmcliDeviceConnect, postNmcliApConnect
  , deleteNmcliConnection
  , setApiConfigUrl,getApiConfigUrl,setApiConfigPort,getApiConfigPort
  , setApiConfigUrlAvahi,getApiConfigUrlAvahi
  } from './api/nmcli.js'
import { Button, Modal, Dialog, Toast, Field, Input} from 'svelma'
const titleize = s => s.replace(/^([a-z])/, (_, r) => r.toUpperCase())

let connectModalActive = false

let unit = {}
let wifiListArray = []
// connections: {"NAME":"ap-5","UUID":"4d2838f2-b53d-4acc-8daf-8871f891771c","TYPE":"wifi","DEVICE":"wlp7s0"}

var isConnecting =''
var isConnectingAP =''
var isRefreshingWifiListCache=''
var isRefreshingWifiList=''
var isRefreshingConnections=''
var isDeletingConnection=''
var wifiListProgressCount=0

function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

const nmcliConnectionShow = function(){
  isRefreshingConnections='is-loading'
  getNmcliConnectionShow()
  .then((result)=>{
    if (Array.isArray(result.data)==true) unit.nmcliConnections = result.data
    else unit.nmcliConnections=[]
    isRefreshingConnections=''
  })
  .catch((error)=>{
    console.log(error.message)
    unit.nmcliConnections=[]
    isRefreshingConnections=''
  })
}
const nmcliConnectionDelete = function(connection){
  isDeletingConnection='is-loading'
  deleteNmcliConnection(connection)
  .then((result)=>{
    isDeletingConnection=''
    nmcliConnectionShow()
  })
  .catch((error)=>{
    console.log(error.message)
    isDeletingConnection=''
  })
}
const nmcliDeviceHotspot = async function(connection){
  const res = await getNmcliDeviceHotspot(connection);
  if(res.error) {
    console.log(res.error.message)
  }
  //unit.nmcliConnections = res;
  console.log('end of activation device hotspot function')
}
const nmcliDeviceWifiListCache = async function(connection){
  isRefreshingWifiListCache='is-loading'
  wifiListProgressCount=1
  getNmcliDeviceWifiListCache()
  .then((result)=>{
    if (Array.isArray(result.data)==true) wifiListArray = result.data
    isRefreshingWifiListCache=''
    wifiListProgressCount=100
  })
  .catch((error)=>{
    isRefreshingWifiListCache=''
    wifiListProgressCount=100
  })
}
const nmcliDeviceWifiList = async function(connection){
  isRefreshingWifiList='is-loading'
  wifiListProgressCount=1
  getNmcliDeviceWifiList(connection)
  .then( async (result)=>{
    if (result.status==210) {
      for (var i=wifiListProgressCount;i<=100;i+=1){
        wifiListProgressCount=i
        await sleep(140) // 100x 140ms =14s
      }
      getNmcliDeviceWifiListCache()
      .then((result)=>{
        if (Array.isArray(result.data)==true) wifiListArray = result.data
        isRefreshingWifiList=''
      })
      .catch((error)=>{
        isRefreshingWifiList=''
      })
    } else {
      wifiListProgressCount=100
      isRefreshingWifiList=''
      if (Array.isArray(result.data)==true) wifiListArray = result.data
      else wifiListArray=[]
    }
  })
  .catch((error)=>{
    console.log(error.message)
    isRefreshingWifiList=''
    wifiListArray=[]
  })
}
const nmcliDeviceConnect = async function(connection){
  isConnecting='is-loading'
  await postNmcliDeviceConnect(connection)
  .then((result)=>{
    isConnecting=''
    nmcliConnectionShow()
  })
  .catch((error)=>{
    console.log(error.message)
    isConnecting=''
  })
}
const nmcliApConnect = function(param){
  isConnectingAP='is-loading'
  postNmcliApConnect(param)
  .then((result)=>{
    isConnectingAP=''
    nmcliConnectionShow()
  })
  .catch((error)=> {
    isConnectingAP=''
    console.log(error.message)
  })
}

//let deleteModalShow=''
let focusedConnection={}
const confirmDelete = function(connection) {
  //console.log(connection)
  focusedConnection = connection
  //deleteModalShow ='is-active'
}

const thenHandler = result => Toast.create(`You ${result ? 'confirmed' : 'canceled'}`)
const deleteConnection = connection => {

  console.log('verwijder connectie: ')
  console.log(connection)
  nmcliConnectionDelete(connection)
}
function confirm(param) {
  var _param=param
  console.dir(_param)
   switch(_param.action) {
     case 'deleteConnection':
       return Dialog.confirm({
         message: 'Do you want to delete this connection?',
         title: "Delete connection: "+ _param.connection.NAME,
         type: 'is-light',
         icon: 'times-circle'
       })
       .then(result=>{
         if (result) {
           _param.connection.key=_param.key
           deleteConnection(_param.connection)
         }})
       .catch(error=>{
         })
     case 'connectAccessPoint':
     return Dialog.confirm({
           message: 'Do you want use this AccessPoint?',
           title: "Use WiFi AccessPoint: "+ _param.ssid + ' (method='+_param.method+')',
           type: 'is-info',
           icon: 'times-circle'
         })
         .then(result=>{
           if (result) {
             console.log('test')
             console.log(_param.method)
             console.log(result)
             nmcliApConnect(_param)
           }
         })
         .catch(error=>{
          })
      return
     case 'deviceConnect':
           return Dialog.confirm({
             message: 'Do you want to (re)connect the sensorbox to this accesspoint?',
             title: "Connect WiFi: "+ _param.connection.NAME,
             type: 'is-info',
             icon: 'times-circle'
           })
           .then(result=>{
             console.log('then')
             if (result) {
               _param.connection.key=_param.key
               nmcliDeviceConnect(_param.connection)
             }
           })
           .catch(error=>{
             console.log('catch')
           })
        return
     default:
       Dialog.confirm('Default action:none')
       .then(thenHandler)
       .catch(error=>{
       })
   }
 }

onMount(async () => {
  // get nmcli general info when module is activated like status of network
  nmcliConnectionShow()
});

</script>

<!--section class="section"-->
<!--div class="container is-max-desktop"-->
  <div class="container">
  <h1>5. Activeren netwerkverbinding</h1>
  <!--div class="columns is-centered"-->
  <div class="info-blok">
  <span>Hier activeert u de netwerkverbind op de sensorkit naar een
    wifi-netwerk. De knop 'Vernieuw' laat de geconfigureerde netwerken zien.
    Met de knop 'AccessPoint' zal de sensorkit een eigen wifi-netwerk starten.<br>
    De knoppen bij de netwerkverbindingen zijn voor het activeren of verwijderen.<br>
    Bij 6. kunt u een nieuw WiFi-netwerk toevoegen.</span>
  </div>
<!--/div-->
  <div>
    <button class="button is-info {isRefreshingConnections}" on:click={nmcliConnectionShow}>Vernieuw</button>
    <button class="button is-info" on:click={nmcliDeviceHotspot}>AccessPoint</button>
  </div>
  {#if unit.nmcliConnections !=undefined }
    {#if unit.nmcliConnections.error !=undefined }
      <div class='general error'> ERROR: {unit.nmcliConnections.error.message}</div>
    {:else}
    <div class="columns is-centered">
      <!--div class="column is-narrow"-->
      <table class="table is-striped is-narrow">
        <thead>
          <tr>
            <th>Name</th>
            <th>Device</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {#each unit.nmcliConnections as connection , i}
          {#if connection.TYPE =='wifi' }
            <tr>
              <td>{connection.NAME}</td>
              <td>{connection.DEVICE}</td>
              <td>
                 <span style="font-size: 2em; color: #3298dc;"
                  todo_use:tooltip={{content: Tooltip, text: 'Connect'}}
                  on:click={()=>
                   {confirm({action:'deviceConnect',key:'uuid',connection:connection})}}
                    aria-label="Open">
                    {#if connection.DEVICE=='--'}
                    <Icon icon={iconFaWifi}></Icon>
                     {:else}
                     <Icon icon={iconFaCheck}></Icon>
                    {/if}
                 </span>
                 <span style="font-size: 2em; color: red;"
                 tooltip="Delete"
                 on:click={()=>
                  confirm({action:'deleteConnection',key:'uuid',connection:connection})}
                   aria-label="Delete">
                 <Icon icon={iconFaWindowClose}></Icon>
                 </span>
                 <!--Button class="is-danger" on:click={()=>
                   confirm({action:'deleteConnection',key:'name',connection:connection})}
                    aria-label="close">Name
                  </Button-->
              <td>
               </td>
            </tr>
            {/if}
          {/each}
<!--        {#if !loading}
            <tr>
              <td colspan="4">
                <section class="section">
                  <div class="content has-text-grey has-text-centered">
                    <p><i class="far fa-3x fa-frown"></i></p>
                    <p>No data</p>
                  </div>
                </section>
              </td>
            </tr>
          {/if}
-->
        </tbody>
      </table>
    <!--/div-->
    </div>
    {/if}
  {/if}
</div>
<!--/section>

<section class="section"-->

<div class="container is-max-desktop">
  <h1>6. Lokale WiFi-netwerken</h1>
  <!--div class="columns is-centered"-->
  <div class="info-blok">
  <span>De onderstaande lijst bevat de WiFi-netwerken welke door de sensorkit
    wordt gezien. Door het wachtwoord bij betreffende WiFi-netwerk in te vullen
    en op de knop met de plus (<Icon icon={iconFaPlus}></Icon>) te drukken wordt het
    WiFi-netwerk als verbinding toegevoegd bij 5.
    <br>Met de knop 'Toon' wordt de laatst opgehaalde lijst getoond. Met
    'Vernieuw' wordt deze lijst geactualiseerd. <br>Let op! Als de sensorkit
    een wifi-netwerk heeft gestart, zal de knop 'Vernieuw' de verbinding
    verbreken en soms niet automatisch vanzelf herstellen.</span>
    </div>
  <!--/div-->
  <div>
    <button class="button is-info {isRefreshingWifiListCache}" on:click={nmcliDeviceWifiListCache}>Toon</button>
    <button class="button is-info {isRefreshingWifiList}" on:click={nmcliDeviceWifiList}>Vernieuw</button>
    <progress class="progress is-info" value="{wifiListProgressCount}" max="100">{wifiListProgressCount}%</progress>
  </div>
  <div class="container wifilist">
    <div class="columns is-centered">
      <!--div class="column is-narrow"-->
      <table class="connection-table table is-striped is-narrow">
        <thead>
          <tr>
            <th>SSID</th>
            <th>In use</th>
            <th>Channel</th>
            <!--th>Rate</th-->
            <th>Signal</th>
            <th>Bars</th>
            <th>Security</th>
            <th>Password</th>
            <th>Connect</th>
          </tr>
        </thead>
        <tbody>
          {#each wifiListArray as accessPoint , i}
          {#if accessPoint.CHAN<14000}
          {#if accessPoint.SSID!='--'}
            <tr>
              <td>{accessPoint.SSID}</td>
              <td>{accessPoint['IN-USE']}</td>
              <td>{accessPoint.CHAN}</td>
              <!--td>{accessPoint.RATE}</td-->
              <td>{accessPoint.SIGNAL}</td>
              <td>{accessPoint.BARS}</td>
              <td>{accessPoint.SECURITY}</td>
               <td>
                 <input class="password" name="Wachtwoord" xtype=password
                  placeholder="Wifi wachtwoord" bind:value={accessPoint.password} />
              </td>
              <td>
                <button name="m1" class="button is-info {isConnectingAP}" on:click|preventDefault|stopPropagation={()=>
                  confirm({action:'connectAccessPoint',ssid:accessPoint.SSID, passwd:accessPoint.password,method:'1'})}
                   aria-label="Add connection">
                   <Icon icon={iconFaPlus}></Icon>
                </button>
              </td>
              <td>
               <button name="m2" class="button is-info {isConnectingAP}" on:click|preventDefault|stopPropagation={()=>
                 confirm({action:'connectAccessPoint',ssid:accessPoint.SSID, passwd:accessPoint.password,method:'2'})}
                  aria-label="Add connection">
                  <Icon icon={iconFaPlus}></Icon>
                </button>
              </td>
            </tr>
            {/if}
            {/if}
          {/each}
<!--        {#if !loading}
            <tr>
              <td colspan="4">
                <section class="section">
                  <div class="content has-text-grey has-text-centered">
                    <p><i class="far fa-3x fa-frown"></i></p>
                    <p>No data</p>
                  </div>
                </section>
              </td>
            </tr>
          {/if}
-->
        </tbody>
      </table>
    <!--/div-->
    </div>
</div>
</div>
<!--/section-->


<Modal bind:active={connectModalActive}>
  <p class="form is-4by3">
    <Field position="is-centered">
      <Input />
      <div class="control"><Button type="is-primary">Button</Button></div>
    </Field>

    <Field grouped position="is-right">
      <Input />
      <div class="control"><Button type="is-primary">Button</Button></div>
    </Field>
  </p>
</Modal>

